import React from 'react';

function Designing(){
    return(
        <div className="page">
            <h2>Designing</h2>
            <p>Art is something I love since childhood. I was <b>drawing</b> often and everyone at school liked my work. I think the <b>passion</b> for drawing came from my dad and brother who also has a talent for drawing. From drawing on paper I went to drawing on a computer where I found there are so many tools and jobs where graphic skills are very useful. Due to <b>curiosity</b> and analytical thinking, I started studying the <b>theory of colors</b>. What colors suit each other. How to choose a color <b>palette</b>. Also how to place objects in the layout so it is pleasant for the eye. The theory behind the art is so enormous that it’s impossible to learn all of it - we can just try to feel it...</p>

            <ul> <span>General skills: </span>
                <li>Artistic sense </li>
                <li>Knowladge of color theory</li>     
                <li>Skills in graphic tools like: Illustrator, Photoshop, Inkscape</li>     
                <li>Skills in designing tools like: Adobe XD and Figma </li>     
                <li>Designing logo and illustrations</li>     
            </ul>
        
        </div>  
    );
}

export default Designing;